import React,{Component} from 'react';
import './Boton.css';

class Boton extends Component{

    render(){
        return(
            <div className="botonAction" onClick={this.estadoActual}>
                <div className={'boton sombra '+this.actual()} >{this.props.nombre}</div>
            </div>
        )
    }
    actual=()=>{
        if(this.props.dir===this.props.actual){
            return 'activo'
        }
        else{
            return ''
        }
    }
    estadoActual=()=>{
        let visor=document.querySelector('.Visor')
        let nElement=1
        let nVista=1
        visor.classList.remove('Arriba')
        visor.classList.remove('Abajo')
        visor.classList.remove('Izquierda')
        visor.classList.remove('Derecha')
        visor.classList.remove('rotar')
        switch(this.props.actual){
            case 'SobreMi': nVista =1;
            break;
            case 'MisTrabajos': nVista=2;
            break;
            case 'MisCertificados': nVista=3;
            break;
            case 'MiBlog': nVista=4
            break;
            case 'Contacto': nVista=5;
            break;
            default:
        }

        switch(this.props.dir){
            case 'SobreMi': nElement =1;
            break;
            case 'MisTrabajos': nElement=2;
            break;
            case 'MisCertificados': nElement=3;
            break;
            case 'MiBlog': nElement=4
            break;
            case 'Contacto': nElement=5;
            break;
            default:
        }

        if(nElement!==nVista){
            if(nElement>nVista){
                setTimeout(()=>{
                    if(window.innerWidth>700){
                        visor.classList.add('Arriba')
                    }
                    else{
                        visor.classList.add('Izquierda')
                    }
                },5
                )
            }
            else{
                setTimeout(()=>{
                    if(window.innerWidth>700){
                        visor.classList.add('Abajo')
                    }
                    else{
                        visor.classList.add('Derecha')
                    }
                },5
                )
            }
        }


        setTimeout(()=>{
            this.props.estado(this.props.dir)},500
        )
    }
}

export default Boton;