import React,{Component} from 'react';
import Menu from './Menu/Menu.jsx';
import Visor from './Visor.jsx';
import './App.css';
import SobreMi from './Paginas/SobreMi.jsx';
import MisTrabajos from './Paginas/MisTrabajos.jsx'
import MisCertificados from './Paginas/MisCertificados.jsx'
import Contacto from './Paginas/Contacto.jsx';
//import Contacto from './Paginas/Contacto.jsx' 

class App extends Component{
  constructor(props){
    super(props)
    this.state={
      alto:0,
      pagina:<SobreMi/>
    }
  }
  render(){
    return(
      <div>
        <div className="App" style={{height:this.state.alto}}>
            <h1 id='miNombre'>EZEQUIEL CICCIOLI</h1>
            <Menu action={this.accionMenu}/>
            <Visor pagina={this.state.pagina}/>
        </div>
      </div>
    )
  }
  componentDidMount=()=>{
    this.alto()
    window.addEventListener('resize',this.alto)
    
  }
  alto=()=>{
    let altoDocumento = document.querySelector('html').clientHeight
    console.log(altoDocumento)
    this.setState({
      alto:altoDocumento
    })
  }
  accionMenu=(Selec)=>{
    let v=undefined;
    switch(Selec){
      case 'SobreMi': v= <SobreMi/>;
      break;
      case 'MisTrabajos': v= <MisTrabajos/>;
      break;
      case "MisCertificados": v= <MisCertificados/>;
      break;
      case "WordPress": this.abrirWordPress();
      break;
      case "Contacto": v= <Contacto/>;
      break;
      default:
    }
    if(v!==undefined)
    this.setState({
      pagina:v
    })
  }
  abrirWordPress=()=>{
    this.setState({
      pagina: <Carga/>
    })
    window.location.href='https://emc014.000webhostapp.com';
  }
}

class Carga extends Component{
  render(){
    return(
      <div className="Cargando">
        <i class="fas fa-spinner fa-pulse fa-10x"></i>
        <h3>Cargando</h3>
      </div>
    )
  }
  hola=()=>{
    alert('hola')
  }
}


export default App;