import React,{Component} from 'react';
import Boton from './Boton.jsx';
import './Menu.css';

class menu extends Component{
    constructor(props){
        super(props)
        this.estados= this.estados.bind(this);
        this.state={
            botones:[
                {Nombre:'Sobre mi',Direccion:'SobreMi'},
                {Nombre:'Mis Trabajos',Direccion:'MisTrabajos'},
                {Nombre:'Mis Certificados',Direccion:'MisCertificados'},
                {Nombre:'Muestra de WordPress', Direccion:'WordPress'},
                {Nombre:'Contacto',Direccion:'Contacto'}],
            estado:'SobreMi',
            nVista:1
        }
    }
    render(){
        return(
            <div className='Menu'>
                <div className="sombra">
                    {this.state.botones.map((a,n)=>{
                        return <Boton 
                        nombre={a.Nombre} 
                        dir={a.Direccion}
                        key={n}
                        actual={this.state.estado}
                        estado={this.estados}/>
                    })}
                </div>
            </div>
        )
    }
    estados(dir){
        this.props.action(dir)
        this.setState({
            estado:dir
        })
    }
}

export default menu;